.Learn_page{
    margin: -5px 0px 0;
    padding: 20px 120px 20px ;
    background-color:#065257;
}

.Learn_callback{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-content: center;
    align-items: center;
    text-align: center;
    text-align: right;
}
.Learn_callback p{
    color: white;
    font-size: 38px;
    font-weight: 500;
    margin-right: -180px;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */

}
.Learn_callback .req_main{
    display: flex;
    justify-content: right;
}
.Learn_callback .req{
    width: 190px;
    display: flex;
    gap: 20px;
    align-items: center;
    background-color: #FDB946;
    font-size: 13px;
    font-weight: 500;
    padding: 10px 15px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}
.Learn_callback .req:hover{
    background: #e2a234;
    transition: 0.3s;
}
.Learn_callback .req i{
    font-size: 13px;
}
.curriculum_By{
 text-align: center;
 color: white;
 padding: 40px;
 margin-bottom: 5px;

}
.curriculum_By h1{
    font-size: 38px;
    font-weight: 500;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */

}
.props_load{
    position: relative;
}
.enrol_banner{
    width: 100%;
    position: fixed;
    bottom: -50;
    left: 0;
    right: 0;
    z-index: 50;
    background-color: #fff;
    padding: 25px 40px;
    margin-top: 25px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 0 5px #000;
    /* display: none; */
    /* visibility: hidden; */
    /* top: 100%; */
}
.enrol_banner.bn_open{
    /* visibility: visible; */
    bottom: 0;
    /* transition: 1s; */
    /* top: none; */
    transition: 0.8s;
}
.enrol_banner.bn_opens{
    bottom: -10%;
}
.enrol_banner .banner p{
    font-size: 14px;
    opacity: 0.7;
    font-weight: 500;
    margin-bottom: 2px;
}
.enrol_banner .banner h3{
    font-size: 16px;
    font-weight: 500;
}
.enrol_banner .banner h3 span{
    font-size: 13px;
    text-decoration: line-through;
}
.enrol_banner .butn button{
    font-size: 16px;
    padding: 13px 25px;
    width: 190px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #000;
    color: #fff;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}
.Button_download{
    margin: 60px 0;
}
.Button_download button{
    width: 300px;
    font-size: 16px;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
}
.Button_download button:hover{
    background-color: #FDB946;
    color: #fff;
    transition: 0.3s;
}
.Button_download button i{
    font-size: 20px;
}
.con_adv{
    display: none;
}
