@media(max-width:1260px){
    .Learn_page{
        margin:-50px 0 0 0px;
    }
    .Learn_page{
        padding: 20px 50px 10px ;
    }
}
@media(max-width:960px){
    .Learn_callback{
        margin-bottom:-10px ;
    }
    .Learn_page{
        margin-bottom: -20px;
    }
    .Learn_callback p{
        font-size:32px ;
    }
    .curriculum_By h1{
        font-size: 32px;
    }
    .enrol_banner{
        display: none;
        /* box-shadow: 0 0 15px #000; */
    }
}
@media(max-width:850px){

    .Learn_callback{
        text-align: center;
        grid-template-columns: repeat(1,1fr);
    }
    .Learn_callback p{
        margin: 0;
        margin-bottom: 15px;
    }
    .Learn_callback .req_main{
        flex-wrap: wrap;
        justify-content: center;
    }
    .con_adv{
        position: fixed;
        background: #0b0b0b91;
        /* background: #fff; */
        /* box-shadow: 0 0 5px #00000093; */
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 40;
        display: flex;
        justify-content: center;
        gap: 50px;
        align-items: center;
        padding: 25px 20px;
    }
    .con_adv button{
        padding: 10px 20px;
        font-size: 14px;
        border: none;
        outline: none;
        border-radius: 5px;
        cursor: pointer;
        display: flex;
        align-items: center;
        gap: 10px;


    }
    .con_adv button i{
        font-size: 12px;
    }
    .con_adv button:first-child{
        background: #FDB946;
        /* color: #fff; */
    }
    .con_adv button:first-child:hover{
        background: #e2a234;
        transition: 0.3s;
    }
    .con_adv button:last-child:hover{
        background: #065257;
        color: #fff;
        transition: 0.3s;
    }
    .Learn_callback .req{
        display: none;
    }
    /* .Learn_callback .req{
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 60;
        border-radius: 0px;
    } */
}
@media(max-width:760px){
    .Learn_page{
        padding: 20px 32px 10px ;
    }
    .Button_download{
        margin: 40px 0 15px;
    }
    .enrol_banner{
        flex-wrap: wrap;
        gap: 20px;
        /* border-radius: 5px; */
    }
    /* .Button_download button{
        margin: 0 auto;
    } */
    
}
@media(max-width:509px){
    .enrol_banner{
        /* justify-content: left; */
        justify-content: space-between;
    }
}
@media(max-width:429px){
    .enrol_banner{
        justify-content: center;

    }
}
@media(max-width:480px){
    .Learn_page{
        padding: 20px 24px 10px ;
    }
}
@media(max-width:407px){
    .con_adv{
        gap: 5px;
    }
    .con_adv button{
        padding: 10px 10px;
    }
}
@media(max-width:360px){
    .enrol_banner{
        justify-content: center;

    }
    .banner{
        text-align: center;
    }
}

@media(max-width:340px){
    .Button_download button{
        width: 98%;
    }
}
@media(max-width:321px){
    .con_adv button{
        padding: 10px 5px;
    }
}

