/* .container_main{
  border-radius: 10px !important;
} */
.container_load{
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
/* .container_main:first-child{
  color: red;
} */
.container_main{
  width: 360px;
}
/* .container_sub .last_p:first-child{
  margin-bottom: 100px;
} */
.container_sub{
  background: #fff;
  padding: 20px;
  border-radius: 5px 5px 0 0;
  height: 200px;
}
.container_sub p{
  opacity: 0.5;
  margin-bottom: 7px;
  font-size: 14px;
  font-weight: 500;
}
.container_sub h6{
  line-height: 20px;
  margin-bottom: 7px;
  font-weight: 500;
  font-size: 17px;
}
.container_sub2{
  background: #D6EDEE;
  padding: 25px 20px;
  border-radius: 0 0 5px 5px;
}
.container_sub ul{
  margin-left: 20px;
  font-weight: 400;
  font-size: 14px;
}
.container_sub ul li{
  padding: 4px 0;
  opacity: 0.7;
  font-size: 12px;
  font-weight: 500;
}
.container_sub ul li::marker{
  opacity: 0.8;
  color: gray;
  font-size: 12px;
}

.container_sub2 h6{
  font-weight: 500;
  font-size: 14px;
}
@media(max-width:1410px){
  .container_main{
    flex-grow: 1;
    flex-shrink: 1;
  }
}
@media(max-width:400px){
  .container_main{
    width: 100%;
  }
}