.props2{
    width: 335px;
}
.props_1{
    padding: 25px 30px 35px;
    background: #065257;
    background: linear-gradient(#065257,#0c666c,#0c656b);
    color: #fff;
    border-radius: 5px 5px 0 0;

}
.props_1 p{
    font-size: 13px;
    margin-bottom: 10px;
    opacity: 0.8;
}
.props_1 h1{
    font-size: 24px;
    margin-bottom: 10px;
    font-weight: 400;
}
.props_1 small{
    font-size: 13px;
}
.props_2{
    width: 100%;
    padding: 15px 20px 25px;
    background: linear-gradient(rgb(0,0,0),rgb(0,0,0),rgba(0,0,0,0.7));
    color: #fff;
    border-radius: 0 0 5px 5px;
    border-top: 2px solid #d2e4e5;

}
.props_2 small{
    font-size: 12px;
    margin-bottom: 5px;
}
.props2 p{
    font-size: 13px;
    font-weight: 400;
}
@media(max-width:400px){
    .props2{
        width: 100%;
    }
}
@media(max-width:754px){
    .props2{
        flex-grow: 1;
    }
}