.Tools_icon{
    display: flex;
    gap: 60px;
    justify-content: center;
    align-items: center;
    padding: 40px ;
}
.Tools_head{
    text-align: center;
    padding: 50px;
}
.Tools_head h1{
    font-size: 38px;
    font-weight: 500;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */
}
.Tools_head h1 span{
    font-weight: bold;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */
}
.Tools_icon{
    margin-top: -30px;
}
.tool_img img{
    width: 150px;
    height: 150px;
    padding: 20px;
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 4px;
    object-fit: contain;
}
@media(max-width:1260px){
    .Tools_Learn{
        margin: 0 0;
    }
    .Tools_icon {
        flex-wrap: wrap;
        gap: 20px;
    }
}
@media(max-width:960px){
    .Tools_head h1{
        font-size: 32px;
    }
    .Tools_icon{
        padding: 20px 0;

    }
}
@media(max-width:400px){
    .tool_img img{
        width: 135px;
        height: 135px;
    }
}
