.Certificate_learn{
    /* margin: 0 25px; */
    background: #065257;
    color: #fff;
}
.certificate_main{
    display: grid;
    justify-content: center;
    align-items: center;

}
.certificate_head {
    margin: 50px 0 30px 0;
}
.certificate_head h1{
    font-size: 38px;
    font-weight: 400;
    text-align: center;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important */
}
.certificate_head h1 span{
    font-weight: 700;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important */
}
.certificate_sub{
    width: 800px;
    padding: 50px;
    border: 20px solid rgb(6, 119, 130);
    /* box-shadow: 0 0 3px blue; */
}
.certificate_start{
    display: flex;
    justify-content:space-between;
    border-bottom: 2px solid #fff;
}
.certify_head{
    margin-bottom: 25px;
}
.certify_head h1{
    font-size: 32px;
    font-weight: 400;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important */
}
.certify_com h1{
    font-size: 38px;
    font-weight: 500;
    line-height: 40px;
    margin-bottom: 20px;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important */
}
.certify_com p{
    opacity: 0.8;
    margin-bottom: 5px;
}
.certify_com h2{
    font-size: 32px;
    font-weight: 400;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */

}
.certify_com .course{
    opacity: 0.8;
    margin-bottom: 10px;
}
.cert_sub2 p{
    font-size: 250px;
    color: #000;
    opacity: 0.2;
}
.certificate_verify{
    text-align: center;
    padding: 50px;
    color: #fff;
}
.certificate_verify h1{
    font-size: 38px;
    font-weight: 400;
    line-height: 35px;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */
}
.certificate_verify h1 span{
    font-weight: 700;
    /* font-family: "Playfair Display", serif !important; */
}
.certificate_verify p{
      padding: 20px;
      font-size: 18px;
      line-height: 26px;
      opacity: 0.7;
}
.footer_last{
    background-color: black;
    text-align: center;
    padding: 30px 0 0 0;
    color: #fff;
    text-align: center;

}
.footer_last .logos{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}
.footer_last p a{
    font-size: 18px;
    text-decoration: none;
    color: #fff;
}
.footer_last p:not(.copy p){
    cursor: pointer;
}
.copy{
    padding: 20px;
}
.copy p{
    font-size: 18px;
}

@media(max-width:1260px){
    .Certificate_learn{
        margin: 0 0 ;
    }     
}
@media(max-width:960px){
    .certificate_head h1{
        font-size: 32px;
    }
    .certify_head h1{
        font-size: 28px;
    }
    .certify_com h1{
        font-size: 32px;
        margin-bottom: 20px;
    }
    .certify_com h2{
        font-size: 28px;
    }
    .certify_com p{
        font-size: 17px;
    }
    .cert_sub2 p{
        font-size: 230px;
    }
    .certificate_sub{
        width: auto;
        border: 15px solid rgb(6, 119, 130);
        margin: 0 32px;
    }
    .certificate_start{
        gap: 30px;
    }
    .certificate_verify{
        padding: 50px 32px;

    }
    .certificate_verify h1{
        font-size: 32px;
    }
    .certificate_verify p{
        font-size: 16px;
        line-height: 25px;
    }
    .footer_last p a{
        font-size: 16px;
    }
    .copy{
        padding: 20px 15px 20px 15px;
    }
    .copy p{
        font-size: 16px;
    }
}
@media(max-width:850px){
.footer_last{
    padding: 30px 0 80px 0;
}
}
@media(max-width:760px){
    .certificate_sub{
        padding: 5px 20px;
    }
}
    @media(max-width:580px){
    .certificate_sub{
        text-align: center;
        padding: 40px 20px 20px;
    }
    .certificate_start{
        flex-wrap: wrap;
        justify-content: center;
    }
    .certificate_start{
        border-bottom: none;
    }
    .cert_sub2 p{
        font-size: 200px;
    }
    .certificate_sub{
        padding: 0px 20px;
    }
    }
    @media(max-Width:480px){
        .certificate_sub{
            margin: 0 24px;
        }

        .certificate_verify{
            padding: 50px 24px;
    
        }
        .cert_sub2 p{
            font-size: 160px;
        }

    }

    