.user_design{
    display: flex;
    gap: 20px;
    justify-content: space-between;
}
.user_design .user_text .user_head{
    margin-bottom: 15px;
}
.user_design .user_text .user_head h1{
    font-size: 52px;
    line-height: 50px;
    font-family: "Lexend", sans-serif;
    /* font-family: serif; */
    /* font-family: "Roboto", sans-serif; */
    /* font-family: "Playfair Display", serif !important */


}
.user_design .user_text .user_content{
    margin-bottom: 30px;
}
.user_design .user_text .user_content p{
    font-size: 18px;
    opacity: 0.8;
}
.user_tag{
    background-color: #236f74;
    border-radius: 5px;
    /* box-shadow: 0 0 5px rgba(0,0,0,1); */

}
.user_tag .tag_main{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    padding: 15px 0;
}
.user_tag .tag_main .tag_txt{
    text-align: center;

}
.user_tag .tag_main .tag_txt h4{
    font-weight: 500;
    margin-bottom: -5px;
}
.user_tag .tag_main .tag_txt small{
    font-size: 12px;
    opacity: 0.8;
}
.user_tag .tag_main .tag_txt:not(:last-child){
    border-right: 2px solid #2d868c;
    /* opacity: 0.4; */
}
.popup_main{
    width: 340px;
    background: #fff;
    color: black;
    border-radius: 5px;
    position: relative;
}
.popup_main .dis_img img{
    position: absolute;
    top: -1%;
    right: 5%;
    width: 70px;
    /* opacity: 0.75; */
}
.popup_main .popup_sub {
    padding: 15px 25px;
}
.popup_main .popup_sub .start_head{
    margin-bottom: 16px;
}
.popup_main .popup_sub .start_head p{
    font-size: 15px;
    font-weight: 500;
    opacity: 0.8;
}
.popup_main .popup_sub .start_head h4{
    font-size: 15px;
    font-weight: 600;
}
.popup_main .popup_sub .fee {
    margin-bottom: 20px;
}
.popup_main .popup_sub .fee .fee_cost p{
    font-weight: 500;
    font-size: 15px;
    opacity: 0.8;
}
.popup_main .popup_sub .fee .fee_cost h1{
    display: flex;
    align-items: center;
    gap: 15px;
}
.popup_main .popup_sub .fee .fee_cost h1 span{
    margin-top: 6px;
    font-size: 15px;
    font-weight: 600;
    text-decoration: line-through;
}
.popup_main .popup_sub .fee .fee_emi{
    font-size: 13px;
    font-weight: 500;
    opacity: 0.8;
}
.popup_main .popup_sub .limited{
    margin-bottom: 10px;
}
.popup_main .popup_sub .limited p{
    font-size: 14px;
    font-weight: 500;
}
.popup_main .popup_sub .limited p.wrong{
    text-decoration: line-through;
    transition: 0.3s;
    background: orange;
}
.popup_main .popup_sub .limited p i{
    font-size: 14px;
    color: blue;
    margin-right: 3px;
}
.popup_main .popup_sub .limited p span{
    color: red;
}
.popup_main .enroll{
    padding: 30px 25px;
    background: aliceblue;
    border-radius: 5px;
}
.popup_main .enroll button{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #000;
    color: #fff;
    padding: 15px 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 16px;
    font-weight: 400;
    cursor: pointer;
}
.popup_main .enroll button.dsb{
    /* background: gray; */
    /* text-align: center; */
    justify-content: center;
    background: #999;
    transition: 0.3s;
}
.form_main{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 50%;
    left: 50%;
    /* right: 0; */
    transform: translate(-50%,-50%);
    z-index: 500;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.8);
    display: none;
}
.form_main.vis{
    display: block;
}
.form_details{
    width: 500px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    padding: 25px 25px 30px;
    border-radius: 0 0 5px 5px;
    color: #000;
    border-radius: 5px;
}
.valid_err{
    text-align: center;
    margin: 20px;
}
.valid_err h1{
    color: red;
    font-size: 24px;

}
.form_details .enroll_head{
    margin-bottom: 30px;
}
.form_details .enroll_head h1{
    color: #065257;
}

.form_details.vis{
    display: block;
}
.form_details .form .cancel{
    text-align: right;
    margin-bottom: 10px;
}
.form_details .form .cancel i{
    font-size: 25px;
    cursor: pointer;
}
.form_details .form .cancel i:active{
    transform: scale(0.9);
}

.form_details .inp_fields .inp_two{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    
}
.form_details .inp_fields .inp{
    margin-bottom: 15px;
}
.form_details .inp_fields label{
    font-size: 15px;
}
.form_details .inp_fields .inp small{
    color: #eee;
    color: red;
}
.form_details .inp_fields label span{
    font-size: 16px;
    color: red;
}
.form_details .inp_fields  input{
    padding: 0 5px;
    margin-top: 2px;
    width: 100%;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 2px;
    font-size: 16px;
    border: 1px solid rgba(0,0,0,0.1);
}
.form_details .btn button{
    padding: 7px 15px;
    border: none;
    outline: none;
    font-size: 16px;
    border-radius: 3px;
    margin-top: 10px;
    cursor: pointer;
}
.form_details .btn button:hover{
    background: #065257;
    color: #fff;
    transition: 0.3s;

}
