.home_highlights .high_text{
    margin-top: 50px;
    text-align: center;
    /* font-size: 2px; */
}
.home_highlights .high_text h1{
    font-size: 30px;
    font-family: "Lexend", sans-serif;
    font-weight: 500;
}
.Home_image{
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 30px 0 60px;
    /* text-align: center; */
    
}
.Home_content{
    display: flex;
    align-items: center;
    gap: 20px;
}
.Home_content img{
    width: 100px;
    height: 100px;
    object-fit: contain;
}
.Home_content p{
    font-size: 18px;
}

