        margin: 0 0px;
        padding: 20px 50px 80px;
    }
    .user_tag .tag_main{
        padding: 10px 10px;
    }
    .popup_main{
        width: 330px;
    }


}
@media(max-width:960px){
    .user_design .user_text .user_head h1{
        font-size: 42px;
        line-height: 50px;
    }
    .tag_main{
        grid-template-columns: repeat(3,1fr) !important;
        text-align: center !important;
    }
    .tag_txt{
        margin-bottom: 10px;
    }
    .user_tag .tag_main .tag_txt:nth-of-type(4),
    .user_tag .tag_main .tag_txt:nth-of-type(3){
