    }
    .home_highlights .high_text h1{
        font-size: 28px;
    }
    .Home_content img{
        width: 90px;
        height: 90px;
        /* border: 1px solid blue; */
        object-fit: contain;
    }
    .Home_content p{
        font-size: 16px;
    }

