        border-right: none;
    }
    .user_tag .tag_main .tag_txt:nth-of-type(3){
        border-right: 2px solid #2d868c;
    }
    .form_main{
        top: 0;
        left: 0;
        right: 0;
        transform: translate(0,0);
    }
    .form_details{
        top: 0;left: 0;
        right: 0;
        transform: translate(0,0);
        border-radius: 0 0 5px 5px;
        width: 100%;
    }
}
