.add{
    /* margin: 0 25px; */
    padding: 25px 80px 40px;

}
.props2_head{
    text-align: center;
    margin-bottom: 45px;
}
.props2_head h1{
    font-size: 38px;
    font-weight: 500;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */

}
.props2_head h1 span{
    font-weight: bold;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */
}
.add_on{
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 50px;
}
.lpa{
    padding: 0 120px;
    width: 100%;
}
.lpa_head{
    text-align: center;
    margin-bottom: 40px;
}
.lpa_head h1{
    font-size: 38px;
    font-weight: 500;
    font-family: "Lexend", sans-serif;
    /* font-family: "Playfair Display", serif !important; */
}

.lpa_text{
    width: 100%;
    padding: 25px;
    text-align: center;
    background: #065257;
    color: #fff;
    margin-bottom: 30px;
    border-radius: 5px;
}
.lpa_text p{
    font-size: 16px;
    margin-bottom: 8px;
}
.lpa_text h1{
    font-size: 22px;
    font-weight: 500;
    /* font-family: "Lexend", sans-serif; */
}
.lpa_text small{
    font-size: 10px;
    opacity: 0.8;
}
.council{
    background: #065257;
    margin-bottom: 10px;
    padding: 20px 30px;
    border-radius: 5px;
}
.council .council_head{
    text-align: center;
    margin-bottom: 30px;
}
.council .council_head h1{
    font-size: 38px;
    font-weight: 500;
    font-family: "Lexend", sans-serif;
    color: #fff;

}
/* .council .council_head h1 span{
    font-weight: bold;
    font-family: "Lexend", sans-serif;
} */
