.council_btn button{
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    border: none;
    outline: none;
    background: #FDB946;
    /* background-color: #fff; */
    margin-top: 30px;
}
.council_btn button:hover{
    background-color: #e2a234;
    transition: 0.3s;
    /* color: #fff; */
    /* color: #fff; */
    cursor: pointer;
}

@media(max-width:1260px){
    .add{
        margin: 0 0px;
        padding: 25px 50px 40px;
    }
    .lpa{
        padding: 0 0px;
    }
}
@media(max-width:960px){
    .props2_head{
        margin-bottom: 35px;
    }
    .props2_head h1{
        font-size: 32px;
    }
    .lpa_head h1{
        font-size: 32px;
    }
    .lpa_head{
        margin-bottom: 30px;
    }
    .council{
        background: none;
        padding: 0;
    }
    .council_txt_main{
        background: #065257;
        padding: 30px 30px;
    }
    .council_txt p{
        font-size: 18px;
    }
    .council .council_head h1{
        font-size: 32px;
        color: #000;
    }
    .council_txt2 p{
        padding-left: 0;
        
    }
}
@media(max-width:760px){
    .add{
        padding: 25px 32px 40px;
    }
}
@media(max-width:480px){
    .add{
        padding: 25px 24px 40px;
    }
    .add_on{
        gap: 10px;
    }
    .council_txt2 p{
        text-align: left;
        margin-bottom: 10px;
        /* padding: 0 0px; */
    }
    /* .lpa_text{
        padding: 25px 15px !important;
